<template>
<div class="login">
  <div class="login-top"  :style="{backgroundImage: 'url('+imageUrl+')'}">
    <div>{{$t("huanYing")}}<br>{{$t('appName')}}</div>
  </div>
  <div class="login-tab">
    <van-tabs v-model="active" @change="changeDisk" color="#1988FF" title-active-color="#1988FF" title-inactive-color="#1988FF" swipeable animated>
      <van-tab :title="$t('LiveAccount')"  to="/signIn"></van-tab>
<!--      <van-tab title="模拟账户" to="/logon"></van-tab>-->
<!--      <van-tab disabled></van-tab>-->
    </van-tabs>
    <router-view @reLogin="reLogin"></router-view>
  </div>
  <div class="version">{{$t('version')}}：{{version}}</div>
</div>
</template>

<script>
  import { Toast } from 'vant';
export default {
  name: 'index',
  data () {
    return {
      // 选项卡
      active: 0,
      version:'2.1.4',
      imageUrl:require('../../assets/loginTop.png'),
    }
  },
  created(){
    let that = this
    this.mui.plusReady(function() {
      plus.runtime.getProperty('H5727BBB2', function(inf) {
        let ver = inf.version;
        that.version = ver
        console.log( this.mui,111111)
        console.log(JSON.stringify(inf))
      })
    })

    // -----------------------------zlq------------------------------
    // 对用户体验进行优化，判断是否有选择过语言，有则按照个人喜好，无则默认英文
    if(localStorage.getItem('i18n')){
      let language = localStorage.getItem('i18n');
      this.$i18n.locale = language
    }else{
      localStorage.setItem('i18n','zh')
      this.$i18n.locale = 'zh'
    }
    // -----------------------------zlq------------------------------
    localStorage.setItem('abnormal','true')
    localStorage.setItem('environment','reality')
    localStorage.setItem('discUrl','121.43.137.150')
    this.active = 0
    this.$router.push('/signIn')
    if(localStorage.jix === 'jix'){
      location.reload()
      localStorage.removeItem('jix')
    }
    if(localStorage.version !== undefined){
      this.version = localStorage.version
    }
    // this.initialization()
  },
  methods:{
    // storage初始化
    initialization(){
      localStorage.removeItem('tickCopy');
      localStorage.removeItem('mainList');
    },
    // 改变盘
    changeDisk(){
      if(this.active === 0){
        console.log('当前为实盘')
        this.$store.state.diUrl = '121.43.137.150'
        // this.$store.commit('changeUrl', '121.43.137.150')
        localStorage.setItem('discUrl','121.43.137.150')
        // store.state.isLogin = true
        localStorage.setItem('environment','reality')
        // console.log(window.webSocket,'实盘');
      }else if(this.active === 1){
        console.log('当前为模拟盘')
        // this.$store.commit('changeEnvironment', 'fictitious')
        // this.$store.commit('changeUrl', '121.43.137.150')
        localStorage.setItem('discUrl','121.43.137.150')
        localStorage.setItem('environment','fictitious')
        this.$store.state.diUrl = '121.43.137.150'
        // this.$store.commit('changeUrl', '121.43.137.150')
        // console.log(window.webSocket,'模拟盘');
      }
    },
    reLogin(e){
      this.$emit('reLogin',e)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../style/login";

</style>



